export const testPromise = () =>{
    console.log(111);
    let myPromise = new Promise((resolve,reject)=>{
        console.log(222222);
        setTimeout(()=>{
            console.log('resolve===',resolve,'reject==',reject);
            if(true){
                console.log(333333);
                setTimeout(()=>{
                    console.log(333334444444)
                })
                resolve('成功')
            }else{
                console.log(4444);
                reject('失败')
            }
        },120)
       
    });
    setTimeout(()=>{
        console.log(55555)
    },100)
    myPromise.then((data)=>{
        console.log(6666666);
        return this
    },(data)=>{
        console.log(777777);
    }).then((data)=>{
        console.log(6667777,data)
    })
    process.nextTick(()=>{
        console.log(11111111)
    })
    setImmediate(function () {
        console.log(12);
    })


    new Promise((resolve,reject) => {
        console.log(888888);
        setTimeout(()=>{
            resolve();
            console.log(14);
        })
        // reject();
    }).then(()=>{
        console.log(999999);
    },()=>{
        console.log(10);
    })
    console.log(13);
    setTimeout(()=>{
        console.log(15)
    },140)

}